[hidden],
.d-n,
.hidden {
    display: none !important;
}

.dekstop {
    @media (max-width: 1024px) {
        display: none !important;
    }
}

.mobile {
    @media (min-width: 1025px) {
        display: none !important;
    }
}

.d-b {
    display: block;
}

.d-ib {
    display: inline-block;
}

.d-f {
    display: flex;
}

.d-if {
    display: inline-flex;
}

.fw-w {
    flex-wrap: wrap;
}

.ai-c {
    align-items: center;
}

.ai-fe {
    align-items: flex-end;
}

.as-c {
    align-self: center;
}

.as-fe {
    align-self: flex-end;
}

.jc-c {
    justify-content: center;
}

.jc-fe {
    justify-content: flex-end;
}

.jc-sb {
    justify-content: space-between;
}

.fcc {
    display: flex;
    justify-content: center;
    align-items: center;
}

// List, ul, ol, dl
.lst-n {
    list-style-type: none;
}

// Margin
.ml-at {
    margin-left: auto;
}

.mr-at {
    margin-right: auto;
}

.mx-at {
    margin-right: auto;
    margin-left: auto;
}

.mt-0 {
    margin-top: 0 !important;
}

.ov-h {
    overflow: hidden;
}

.fw-700 {
    font-weight: 700;
}

.fs-i {
    font-style: italic;
}

.tt-u {
    text-transform: uppercase;
}

.tt-c {
    text-transform: capitalize;
}

.ta-c {
    text-align: center;
}

.td-u {
    text-decoration: underline;
}

.w-100 {
    width: 100%;
}

.w-50 {
    width: 50%;
}

.fs-20 {
    font-size: 20px;
}

.fs-15 {
    font-size: 15px;
}

.w-70 {
    width: 70%;
}

.h-100 {
    height: 100%;
}

.cl-xanh-1 {
    color: map-get($mau, xanh-1);
}

.cl-xanh-2 {
    color: map-get($mau, xanh-2);
}

.cl-xanh-3 {
    color: map-get($mau, xanh-3);
}

.cl-vang {
    color: map-get($mau, vang);
}

.cl-xam {
    color: map-get($mau, xam);
}

.cl-trang {
    color: map-get($mau, trang);
}

.bg-xanh-1 {
    background: map-get($mau, xanh-1);
}

.bg-xanh-2 {
    background: map-get($mau, xanh-2);
}

.bg-xanh-3 {
    background: map-get($mau, xanh-3);
}

.bg-vang {
    background: map-get($mau, vang);
}

.bg-xam {
    background: map-get($mau, xam);
}

.bg-xam-opacity-2 {
    background: rgba(map-get($mau, xam), $alpha: .2)
}

.bg-trang {
    background: map-get($mau, trang);
}

.border-vang-1px {
    border: 1px solid map-get($mau, vang) !important;
}

.opacity--2 {
    opacity: .2;
}

.pe-n {
    pointer-events: none;
}